@import "../../globalSASS/global.scss";
.sing-up {
	display: flex;
	flex-direction: column;
	align-items: center;

	&_pos {
		position: absolute;
		top: 55%;
		left: 50%;
		transform: translate(-50%, 0);
		z-index: 2;
	}

	&__title {
		color: $colorWhite;
		font-size: 42 / 16 + em;
		letter-spacing: 0.025em;
		margin: 0;
	}

	&__text {
		margin: 0;
		width: 10em;
		font-size: 40 / 16 + em;
		letter-spacing: 0.025em;
		color: $colorPurple;

		margin-top: 2.3%;
		margin-left: 4.6%;
	}

	&__button {
		margin-top: 7.8%;        
	}

	@media screen and (max-width: 940px) {
		font-size: 13 / 16 + em;
	}
}
