@import "../../globalSASS/global.scss";
.video {
	height: 88vh;

	&__wrap {
        position: relative;
        margin-left: auto;
        margin-right: auto;
        margin-top: 6%;
        padding-bottom: 45%;
        width: 74.5vw;
		height: 0;
		overflow: hidden;
	}

	&__youtube {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
        height: 100%;
        border-width: 0;
		outline-width: 0;
	}
}
