@import "../../globalSASS/global.scss";
.coordinates {
    position: relative;
    height: 100vh;

   

    &__adress {
        width: 40%;

        color: $colorWhite;
        font-size: 36 / 16 + em;
        text-align: center;
        letter-spacing: 0.025em;

        margin-top: 2.05em;
        margin-left: auto;
        margin-right: auto;

        &_accent {
            color: $colorPurple;
        }

    }
}