@import "../../globalSASS/global.scss";
.button {
		font-family: 'MuseoCyrl-100', sans-serif;
		font-size: 30 / 16 + em;
		font-weight: 700;
		text-transform: uppercase;
        color: #060606;
		letter-spacing: 0.02em;

		padding: 1.4em 3.2em;

		background-color: $colorPurple;
		border: none;
		border-radius: 100px;
		
		cursor: pointer;
		outline: none;
}