@import "../../globalSASS/global.scss";
.title {
    margin: 0;
    text-align: center;
    font-size: 40 / 16 + em;
    text-transform: uppercase;
    color: $colorWhite;

    &_pos {
        padding-top: 4.8%;
    }

    &_big-space {
        padding-top: 8%;
        letter-spacing: 0.02em;
    }

    &_contacts {
        font-size: 30 / 16 + em;
        padding-top: 8.5em;
    }

    &_masters {
        padding-top: 2.8em;
    }

    &_examples {
        padding-top: 1.1em;
    }

    &_price {
        padding-top: 0.7em;
    }

    &_coordinates {
        padding-top: 2em;
    }
}