@import "../../globalSASS/global.scss";
@font-face{
    font-family: 'edosz';
    src: url('fonts/edosz.ttf') format('truetype');
    font-style: normal;
    font-weight: normal;
}

@font-face {
    font-family: "MuseoCyrl-100";
  
    src: url("./fonts/9435.ttf") format("truetype"), /* Safari, Android, iOS */
      url("./fonts/9435.woff") format("woff"), /* Modern Browsers */
      url("./fonts/9435.woff2") format("woff2"); /* Modern Browsers */
    font-weight: normal;
    font-style: normal;
  }

.body {
    margin: 0;
    font-family: 'MuseoCyrl-100' ,serif;
    font-size: 16px;
    background-color: #060606;

    @media screen and (max-width: 1680px){
        font-size: 14px;
    }

    @media screen and (max-width: 1525px){
        font-size: 12px;
    }

    @media screen and (max-width: 1300px) {
        font-size: 10px;
    }
}