@import "../../globalSASS/global.scss";
.price {
	position: relative;
	height: 100vh;

	background-image: url('img/apparat.png');
	background-position: center;
	background-size: cover;
	// background-repeat: no-repeat;
	// background-color: red;

	&__wrap {
		position: relative;
		display: flex;
		justify-content: space-between;

		margin-left: auto;
		margin-right: auto;
		margin-top: 8%;
		width: 90%;

		
	}

	&__card {
		position: relative;
		width: 28%;
	}

	&__description {
		padding-left: 3em;
        padding-right: 0.7em;
        padding-top: 1.5em;

		color: $colorWhite;

		@media screen and (max-width: 970px) {
			padding-left: 1em;
			padding-top: 1em;
		}
	}

	&__title {
        margin: 0;
		font-size: 36 / 16 + em;
        font-weight: 700;
        letter-spacing: 0.03em;
		color: $colorPurple;
	}

	&__cost {
		font-size: 30 / 16 + em;
		letter-spacing: 0.04em;

		margin-top: 1.2em;

		&_accent {
			font-size: 36 / 30 + em;
			font-weight: 700;
			color: $colorPurple;
        }
        
        &_min {
            margin-top: 0.8em;
        }
	}

	&__text {
        font-size: 30 / 16 + em;
        letter-spacing: 0.03em;

		margin-top: 1.3em;

        &_min {
            letter-spacing: 0;
        }

        &_correct {
            margin-top: 1em;
        }
	}

	@media screen and (max-width: 1680px) {
		font-size: 0.9em;
	}


	@media screen and (max-width:1110px) {
		font-size: 13 / 16 + em;
	}
}
