@import "../../globalSASS/global.scss";
.background {
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 94%;

    transform: translate(-50%, 0);

    display: flex;
    justify-content: space-between;
    z-index: 1;

    &__img {
        height: 73.5vh;
        @media screen and (max-width: 1680px) {
            height: 37.8vw;
        }
    }

    
}