@import "../../globalSASS/global.scss";
.request {

    height: 100vh;
	&__title {
        margin: 0;
        width: 18em;
		font-size: 36 / 16 + em;
        text-transform: uppercase;
        line-height: 1.35em;
        letter-spacing: 0.02em;
        color: $colorWhite;
        margin-top: 5.2em;
        margin-left: 35.5%;

        &_accent {
            color: $colorPurple;
        }
    }
    
    &__form {
        display: flex;
        flex-direction: column;
        width: 38.5%;
        margin-top: 4.9em;
        margin-left: auto;
        margin-right: auto;
    }

    &__label {
        width: 100%;
        display: flex;
        flex-direction: column;

        &_phone {
            margin-top: 4.4em;
        }
    }

    &__punct {
        font-size: 36 / 16 + em;
        font-weight: bold;
        letter-spacing: 0.02em;
        color: $colorPurple;
    }

    &__input {
        margin-top: 0.8em;
        font-size: 36 / 16 + em;
        color: $colorWhite;
        background-color: transparent;
        border: none;
        border-bottom: 1px solid $colorPurple;
        outline-style: inherit;
    }

    &__button {
        width: 12.8em;
        padding: 1em 0;
        margin-top: 4.7em;
        margin-left: auto;
        margin-right: auto;
    }
    
}
