@import "../../globalSASS/global.scss";
.our-masters {
	position: relative;
	height: 100vh;

    &__container {
        position: relative;
        height: 37vw; 
		margin-top: 8.4vw;
    }

	&__wrap {
		position: absolute;
		right: 0;
		width: 50%;
		overflow: auto;

		z-index: 3;
	}

	&__card {
		opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
		width: 100%;
		height: 100%;
		-webkit-transition: opacity 1s;
		-moz-transition: opacity 1s;
		-o-transition: opacity 1s;
		transition: opacity 1s;

		&_show {
			opacity: 1;
		}
	}

	&__img {
		position: absolute;
		bottom: 0.35%;
		width: 54%;

		z-index: 1;
	}

	&__description {
		display: flex;
		flex-direction: column;

		width: 90%;

		color: $colorWhite;
		padding-left: 1.4em;
        padding-top: 2.3em;
        
		overflow: hidden;
		

	}

	&__name {
		margin: 0;
		font-size: 48 / 16 + em;
		font-weight: 700;

	}

	&__text {
		margin: 0;
		font-size: 30 / 16 + em;
		padding-left: 0.2em;
		padding-top: 0.3em;
		letter-spacing: 0.025em;

		&_accent {
			color: $colorPurple;
			font-size: 36 / 30 + em;
		}
	}

	&__review {
        position: relative;
		margin: 0;
		letter-spacing: 0;

		padding-top: 0.95em;

		@media screen and (max-width:870px) {
			padding-top: 0.2em;
		}
    }
    
    &__link { 
        cursor: pointer;
        &:hover {
            text-decoration: underline;
        }
    }

	&__price {
		margin: 0;
		margin-top: 0.9em;
		letter-spacing: 0.02em;

		@media screen and (max-width:870px) {
			margin-top: 0em;
		}
	}

	&__button {
		margin-top: 1.4em;
		margin-left: 0.4em;
		width: 12.8em;
		padding: 1.1em 0;

		@media screen and (max-width:1680px) {
			width: 10em;
		}

		@media screen and (max-width:870px) {
			font-size: 1.5em;
			padding: 1em;
			margin-top: 1em;
		}
	}

	&__arrow {
		position: absolute;
		top: 50%;
		width: 5vw;
        cursor: pointer;
        
        transform: translate(0, -50%);

		&_l {
			left: 8%;
		}

		&_r {
			right: 8%;
		}

		&_hide {
			display: none;
		}
	}


	@media screen and (max-width: 1820px) {
		font-size: 14 / 16 + em;
	}

	@media screen and (max-width: 990px) {
		font-size: 12 / 16 + em;
	}

	@media screen and (max-width: 870px) {
		font-size: 11 / 16 + em;
	}
	
}	
