@import "../../globalSASS/global.scss";
.contacts {
    height: 100vh;
    background-image: url('img/saloon.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    &__wrap {
        width: 12em;
        margin-left: auto;
        margin-right: auto;
        margin-top: 1.9em;
        font-size: 36 / 16 + em;
        letter-spacing: 0.02em;
        line-height: 1.4em;
        color: $colorWhite;
    }

    &__link {
        color: $colorPurple;
        text-decoration: none;

        &:hover {
            color: $colorWhite;
            text-decoration: underline;
        }

        &:active {
            color: #c9c9c9;
        }

        &_svg {

            &:hover {
            text-decoration: none;
            }
        }
    }

    &__social {
        margin-left: 8%;
        width: 100%;
        margin-top: 1.8em;
    }

    &__svg {
        width: 4em;
        height: 3.2em;
        cursor: pointer;

        &_inst {
            margin-left: 8%;
        }

        &:hover {
            --primary-color: #c4a7da;
        }
        &:active {
            --primary-color: #a88dbd;
        }
    }
    
}