@import "../../globalSASS/global.scss";
.card {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 27.9vw;

    &__img {
        margin-top: 20%;
        margin-bottom: 18%;
        width: 33%;
        min-width: 160px;

    }

    &__text {
        margin: 0;
        font-size: 30 / 16 + em;
        letter-spacing: 0.02em;
        color: $colorPurple;

        padding-left: 1.5em;
        padding-top: 1em;
        padding-right: 0.5em;

        @media screen and (max-width: 970px){
            padding-left: 0.5em;
            padding-top: 0.5em;
        }
    }

    @media screen and (max-width: 1110px) {
        font-size: 14 / 16 + em;
    }

    @media screen and (max-width: 890px) {
        font-size: 12 / 16 + em;
    }

    
}