@import "../../globalSASS/global.scss";
.carousel {
    position: relative;
    width: 100%;
    height: 100%;

    &__item {
        position: absolute;
        bottom: 0;
        right: 0;

        width: 100%;

        opacity: 0;
        z-index: 1;

        -webkit-transition: opacity 1.5s;
        -moz-transition: opacity 1.5s;
        -o-transition: opacity 1.5s;
        transition: opacity 1.5s;

        &_show {
            opacity: 1;
            z-index: 2;
        }
    }

    &__arrow {
        position: absolute;
        top: 50%;
        width: 5vw;
        transform: translate(0, -50%);
        cursor: pointer;

        &_l {
            left: 20%;
        }

        &_r {
            right: 20%;
        }
    }


}