@import "../../globalSASS/global.scss";
.menu {
	position: sticky;
	top: 0;

	display: flex;
	align-items: flex-end;

	padding-top: 0.5vh;
	padding-bottom: 0.5vh;
	height: 5.2vh;
	width: 100%;

	background-color: rgba($color: #060606, $alpha: 0.8);

	z-index: 10;

	&__wrap {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 88.5%;

		margin-left: 5.4%
	}

	&__link {
		font-size: 36 / 16 + em;
		text-decoration: none;
		color: $colorPurple;
		letter-spacing: 0.05em;

		&:hover {
			color: rgba($color: $colorPurple, $alpha: 0.8);
		}

		&:active {
			color: #6c706b;
		}

		&_phone {
			letter-spacing: 0.02em;
		}
	}

	&__phone {
		display: flex;
		align-items: center;
		cursor: pointer;
	}

	&__svg {
		width: 50 / 16 + em;
		height: 50 / 16 + em;
		margin-right: 1.6em;
	}

	@media screen and (max-width: 1010px) {
		.menu {
			&__phone {
				display: none;
			}
			&__svg {
				display: none;
			}
		}
	}
}
