@import "../../globalSASS/global.scss";
.frame {

	width: 67.2vw;
	height: 100%;

	&_pos {
		position: absolute;
		left: 50%;
		transform: translate(-50%, 0%);
	}

	&_logo {
		position: absolute;
		left: 50%;
		top: 5%;
		transform: translate(-50%, 0%);

		width: 22.5vw;
		height: 22.2vw;
	}

	&_vertical {
		position: absolute;

		left: 50%;
		

		transform: translate(-50%, 0%);

		width: 36.8vw;
		height: 100%;
	}

	&_price {
		position: relative;

		width: 21vw;
		height: 27vw;

		@media screen and (max-width:930px){
			width: 26vw;
			height: 26 * (27 / 21) + vw;
		}
	}

	&_coordinates {
		position: relative;
		width: 52vw;
		height: 36.5vw;
		margin-left: 24.2%;
		margin-top: 2.2%;
	}

	&_card {
		position: relative;
		width: 100%;
		height: 13.7vw;
	}

	&__box-1 {
		position: absolute;
		top: 0;
		left: 0;

		width: 96%;
		height: 94.4%;

		border: 5px solid rgba($color: #fff, $alpha: 0.3);
		z-index: 2;

		@media screen and (max-width: 1350px) {
			border-width: 3px;
		}



		&_vertical {
			width: 91.7%;
			height: 93.3%;
		}

		&_price {
			width: 90.5%;
			height: 92.8%;
		}

		&_coordinates {
			width: 92.1%;
			height: 93.4%;
		}

		&_logo {
			width: 92%;
			height: 91.5%;
		}
		&_card {
			width: 93.8%;
			height: 89.6%;
		}
	}

	&__box-2 {
		position: absolute;
		bottom: 0;
		right: 0;

		width: 96%;
		height: 94.4%;

		border: 5px solid rgba($color: #fff, $alpha: 0.3);
		z-index: 2;

		@media screen and (max-width: 1350px) {
			border-width: 3px;
		}

		&_vertical {
			width: 91.7%;
			height: 93.3%;
		}

		&_price {
			width: 90.5%;
			height: 92.8%;
		}

		&_coordinates {
			width: 92.1%;
			height: 93.4%;
		}
		&_logo {
			width: 92%;
			height: 91.5%;
		}
		&_card {
			width: 93.8%;
			height: 89.6%;
		}
	}

	&__content {
		position: absolute;
		top: 4.6%;
		left: 3.5%;
		width: 93%;
		height: 90.7%;

		background-color: $frameBG;
		z-index: 3;

		&_vertical {
			top: 5.9%;
			left: 7.5%;
			width: 85.4%;
            height: 88.4%;
            background-color: transparent;

            overflow: hidden;
		}

		&_price {
			top: 5.5%;
			left: 7.5%;
			width: 84%;
			height: 88.5%;
			z-index: 1;

		}

		&_coordinates {
			display: flex;
			align-items: center;
			justify-content: center;

			top: 5.5%;
			left: 7.5%;
			width: 85.2%;
			height: 89%;
			z-index: 3;
			background: none;
		}
		&_logo {
			top: 7%;
			left: 7%;

			display: flex;
			align-items: center;
			justify-content: center;

			width: 86%;
			height: 86%;
			background: none;
		}
		&_card {
			top: 7.5%;
			left: 5%;

			width: 90%;
			height: 85%;

			overflow: hidden;
		}
	}
}
